import axios from 'axios'
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import "./FutureBar.css"
export class FutureBar extends Component {
    constructor(props){
        super(props)
        this.state = {
            isLiked: false,
            isBodyOpen: false,
            likes: this.props.data.likes
        }
        this.handleBody = this.handleBody.bind(this)
        this.handleLike = this.handleLike.bind(this)
        this.handleDislike = this.handleDislike.bind(this)
    }
    handleBody(){
        console.log("CLIKED>>>>>");
        this.setState({isBodyOpen: !this.state.isBodyOpen})
    }
    async handleLike(){
        this.setState({isLiked:true})
        try {
            await axios.get('/trackAction/',{params: {id: this.props.data.selfId, action: 'like'}})
            this.setState({likes: this.state.likes + 1})
        } catch (error) {
            this.setState({isLiked: false})
        }
    }
    async handleDislike(){
        this.setState({isLiked:false})
        try {
            await axios.get('/trackAction/',{params: {id: this.props.data.selfId, action: 'dislike'}})
            this.setState({likes: this.state.likes - 1})
        } catch (error) {
            this.setState({isLiked: true})
        }
    }
    render() {
        return (
            <div className="futurebar-main">
                <div className={this.state.isBodyOpen? "futurebar-box-active" : "futurebar-box"}>
                    <div className="futurebar-head" onClick={this.handleBody}>
                        <span></span>
                        <p>{this.props.data.title}</p>
                    </div>
                    {this.state.isBodyOpen?
                        <div className="futurebar-body">
                            <p>{this.props.data.desc && this.props.data.desc}</p>
                                <div className="futurebar-btn">
                                {this.state.isLiked?
                                    <img height="20px" width="20px" className="ccc-like-btn" onClick={this.handleDislike} src="/media/images/fill-heart.png" alt="fill-heart"/>
                                :
                                    <img height="20px" width="20px" onClick={this.handleLike} src="/media/images/empty-heart.png" style={{opacity: '0.8'}} alt="empty-heart"/>
                                }
                                <span>{this.state.likes} likes</span>
                                <Link to={`/track/${this.props.data.selfId}`}>
                                    <button>Open Track</button>
                                </Link>
                                <Link to={`/course/${this.props.data.selfId}`}>
                                    <button>Resources</button>
                                </Link>
                            </div>
                        </div>
                    :null}
                    
                </div>
            </div>
        )
    }
}
export default FutureBar